

.community-tabs-admin-control-row {
    display: flex;
    flex-direction: row;
  }
  
  .community-tabs-admin-control-col {
    display: flex;
    flex-direction: column;
  }
  
  .community-tabs-admin-detail {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
  }
  
  .community-tabs-admin-control-cell-col-label {
    width: 95%;
    align-self:center;
    flex-direction: column;
  
    &.displayContent {
      align-self:unset;
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }
  
  .community-tabs-admin-control-cell-col {
    width: 95%;
    flex-direction: column;
  }  
  
  .community-tabs-admin-control-cell-label {
    width: 30%;
    align-self:center;
  }
  
  .community-tabs-admin-control-cell {
    width: 60%;
  }  
  
  @media (max-width: 576px) {
    .community-tabs-admin-control-cell {
      width: 95%;
    }  
  }
  
.mat-cell-action {
    &.moveUpAction {
      padding-top: 8px;
    }
}

.community-tabs-admin-control-cell {
  &.select {
    margin-left: unset;
  }

  &.text {
    margin-left: 5px;
  }

  &.labelText {
    padding: 10px;
  }
}

.actionPanel {
  padding-top: 15px;
  
  .actionPanel-cancel {
    margin-left: 15px;
  }
  
  .actionPanel-save {
    margin-left: 15px;
  }
}
