@import url('https://fonts.googleapis.com/css?family=Roboto');

@import url('https://fonts.googleapis.com/icon?family=Material+Icons');

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

.highlight, .highlight:hover {
  background: #bde8fa!important;
}

.filter-spacer {
  line-height: 58px;
}

.sticky-table-container-inner {
  overflow: auto;
  height: 100%;
}

.dropdown-sticky-filter {
  margin-top: 15px;
}

.th-sticky {
  position: relative;
  top: 0;
  display: inline-flex;
  line-height: 48px;
  /* height of header */
}

.add {
  margin-top: 15px;
}

.changeForm {
  font-size: 14px !important;
}

.reset-filter {
  top: -47px;
  position: absolute;
  margin-right: 5px;
  right: 1rem;
  height: 1rem;
  width: 1rem;
  z-index: 1000;
}

.mat-cell-image {
  max-width: 40px;
  max-height: 40px;
  vertical-align: middle;
}

.mat-cell-action {
  background: transparent;
  border: 0px;
  vertical-align: middle;
}

.mat-cell-action:hover {
  cursor: pointer;
}

.mat-dialog-content > .MuiPaper-root {
  max-width: 80vw; 
  width: 90%; 
  position: static; 
}

.grid-overlay {
  width: 100%;
  left: 0%;
}

.grid-overlay-msg {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  vertical-align: middle;
}

.spinner-container {
  position: absolute;
  display: flex;
  margin: auto;
  width: 100%;
  height: 100%;
}

.no-records {
  margin-left: 15px;
}

.grid-toolbars {
  display: inline-flex;
  white-space: nowrap;
}

.gridTitle {
  color: white;
  font-size: 1.2rem;
  font-weight: 1;
  margin-left: 1.5rem;
  max-width: 500px;
}

@media (max-width: 599px) {
  .gridTitle {
    max-width: 225px;
  }
}

.gridTabContainer {
  display: block;
  justify-content: center;
  margin: 0 2%;
  border-radius: 4px;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.gridContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3rem;
  margin: 0 auto;
  border-radius: 5px 5px 0 0;
  background-color: #0093d0;
}

.displayText {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.button-text {
  text-decoration: none;
  background: transparent;
  color: blue;
  text-align: left;
}

mat-paginator-range-label {
  white-space: nowrap;
  margin: 0 16px 0 12px !important;
}

.pager {
  display: flex;
  margin-top: 5px;
  padding-left: 15px;
  background-color: transparent;
  border: unset;
}

.pl5 {
  padding-left: 5px;
}

.browse {
  padding-right: 15px;
}

.form-row {
  opacity: 1 !important;
}

.form-mode {
  opacity: 0.25;
  transition: 0.5s ease-in-out all;
  pointer-events: none;
}

.mat-typography {
  color: gray;
  margin-left: 15px;
  padding-top: 2px
}

.no-records-message {
  padding-left: 24px;
  padding-top: 15px;
}

.info {
  transform: scale(0.7);
  cursor: pointer;
  margin-left: -2px;
  margin-top: 2px;
}

.info:hover {
  cursor: hand;
}

.modal-panel {
  margin: 0;
  padding: 5px;
}

.apricus-table {
  background: white;
  font-family: Roboto,Helvetica Neue,sans-serif;
  display: block;
}

.mat-row, .mat-footer-row {
  max-height: 40px !important;
  min-height: 40px !important;
}

.mat-row:hover {
  background: #fafafa;
  cursor: pointer;
}

.mat-typography {
  color: gray;
  margin-left: 15px;
}

.mat-row, .mat-header-row, .mat-footer-row, .apricus-form {
  display: flex;
  border-width: 0;
  border-bottom-width: 1px;
  border-style: solid;
  align-items: center;
  box-sizing: border-box;
}

.apricus-form {
  flex: 1 1;
  border-bottom-width: 0px;
}

.MuiInputBase-formControl {
  background: transparent;
}

.multiline-snackbar {
  white-space: pre-wrap;
}

.required-field::after {
  content: "*";
  color: red;
}

.mat-sort-header-container {
  display: flex;
  cursor: pointer;
  align-items: center;
  letter-spacing: normal;
  outline: 0;
}

.mat-focus-indicator {
  position: relative;
}

.mat-sort-header-content {
  text-align: center;
  display: flex;
  align-items: center;
}

.mat-header-row {
  min-height: 56px;
  position: sticky;
}

.mat-form {
  display: flex;
  flex-direction: column;
  width: max-content;
  white-space: nowrap;
  align-items: flex-start;
}

.mat-row, .mat-header-row, .mat-footer-row, th.mat-header-cell, td.mat-cell, td.mat-footer-cell {
  border-bottom-color: #0000001f;
}

.mat-header-cell {
  display: flex;
  flex-direction: column;
  width: max-content;
  white-space: nowrap;
  align-items: flex-start;
}

.mat-header-cell {
  font-size: 12px;
  font-weight: 500;
  color: #0000008a;
}

.mat-cell, .mat-header-cell, .mat-footer-cell {
  flex: 1 1;
  overflow: hidden;
  word-wrap: break-word;
  min-height: inherit;
}

.mat-cell:first-of-type, .mat-header-cell:first-of-type, .mat-footer-cell:first-of-type {
  padding-left: 24px;
}

.mat-cell:last-of-type, .mat-header-cell:last-of-type, .mat-footer-cell:last-of-type {
  padding-right: 24px;
}

.mat-cell, .mat-header-cell {
  padding: 0 5px;
  padding-right: 15px;
}

.mat-cell .select, .mat-header-cell .select {
  max-width: 50px;
  padding-left: 5px;
  padding-right: 5px;
}

.mat-cell, .mat-footer-cell {
  color: #000000de;
}

.mat-cell, .mat-footer-cell {
  font-size: 14px;
  line-height: 48px;
}

.mat-paginator {
  display: block;
}

.mat-paginator, .mat-paginator-page-size .mat-select-trigger {
  color: #0000008a;
}

.mat-paginator {
  background: white;
}

.mat-paginator, .mat-paginator-page-size .mat-select-trigger {
  font-family: Roboto,Helvetica Neue,sans-serif;
  font-size: 12px;
}

.mat-paginator, .mat-paginator-page-size .mat-select-trigger {
  color: #0000008a;
}

.mat-paginator {
  background: #fff;
}

.apricus-container {
  padding: 0 2%;
  margin-top: 25px;
}

*, :after, :before {
  box-sizing: border-box;
}

.actionPanelAddButton, .actionPanelEditButton, .actionPanelCancelButton, .actionPanelSaveButton, 
.actionPanelDeleteButton, .settings-button {
  background: transparent;
  border: 0;
  padding: 6px;
  cursor: pointer;

  .settings-button {
    color: black !important;
  }
}

.mat-dialog-title {
  text-align: center;
  font-weight: bold !important;
}

.confirmation-actions {
  justify-content: center !important;
}

.MuiInput-input {
  margin: 5px 0px !important
}

.mat-cell-select {
  left: -12px;
}

.scaled-icon {
  transform: scale(0.70);
  margin: 0;
  padding: 0;
  top: 6px;
  position: relative;
}

.footer {
  text-align: center;
  margin-left:auto;
  margin-right: auto;
  margin-top: 5px;
  position: fixed;
  width: 100%;
  background-color : #1b75bc;
  color: white;
  bottom: 0;
  left:0;
  max-height:40px;
}

.full-featured-editor {
  padding: 15px;
}

.spinner-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.spinner-progress {
  color: #1b75bc;
  position: absolute;
}

.spinner-box {
  position: fixed;
  top: 30%;
  left: 10%;
  height: 35%;
  opacity: 0.85;
  background-color: lightgray;
  z-index: 500;
  border-radius: 15px;
  border: solid 1px;
  width: 80%;

  @media (min-width: 580px) {
      width: 30%;
      left: 30%;
  }
}

.spinner {
  position:relative;
  box-sizing: border-box;
  display:inline-block;
  padding:0px;
  left: 48%;
  top: 48%;
  margin: -20px 0 0 -25px;
  z-index: 1500;
}

.message {
  position:absolute;
  display:inline-block;
  padding: 25px;
  bottom: 0;
  font-size: larger;
  font-weight: bolder;
}

.community-admin-welcome {
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
}

.custom-module-error {
  color: red;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.error {
  color: red;
  font-weight: bold;
  padding-bottom: 5px;
}

.information {
  color: black;
  font-weight: bold;
  padding-bottom: 5px;
}

.menu-items-nested {
  color: white;
  height: fit-content;
  background-color: #1976d2;
  border: 1px solid lightgray;
}

.menu-item-class {
  /* line-height: 1.75; */
  vertical-align: top;
}

.menu-nested {
  @media (min-width: 599px) {
    display: flex;
    flex-direction: row;
    min-width: 80px;
    & li {
      padding: 6px 8px;
    }

    & .material-icons {
      transform: scale(.75);
      // border: 1px solid white;
      top: 8px;
      position: absolute;
      margin: 2px 2px 0 0;
    }
  }
}

.apricus-menu-item {
  text-transform: none;
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 500;
  font-size: 0.875rem;
}

.MuiList-root {
  padding: 0 !important;
}

.mat-form-row {
  padding: 5px;
  flex-direction: row;
}

.apricus-table-mobile {
  overflow-y: scroll;
}

.table-bound-control-row {
  display: flex;
  flex-direction: row;
}

.table-bound-control-cell-label {
  width: 30%;
  justify-items: left;
  align-items: flex-start;
  flex-direction: row;
  padding: 5px;
  white-space: wrap;
}

.table-bound-control-cell {
  width: 70%;
  align-items: flex-start;
  justify-items: left;
  flex-direction: row;
  padding: 5px;
}  

.mobile-col-basis, .mat-stateless-table {
  & .table-bound-control-row {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  
  & .table-bound-control-cell-label {
    width: 100%;
    justify-items: left;
    align-items: flex-start;
    flex-direction: column;
    padding: 5px;
    white-space: wrap;
    font-weight: bold;
  }
  
  & .table-bound-control-cell {
    width: 100%;
    align-items: flex-start;
    justify-items: left;
    flex-direction: column;
    padding: 5px;
  }
}

@media (max-width: 576px) {
  .mat-pagination {
    & p {
      max-width: 70px;
      padding: 0;
      margin: 0;
    }

    & p:first-of-type {
      align-items: flex-start;
    }
  }

  .apricus-form {
    display: unset;
  }
  
  .card-row {
    padding: 1px;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
  }

  .bound-control-row {
    display: flex;
    flex-direction: column;
    padding: 5px;
  }
  
  .bound-control-cell-label {
    width: 95%;
    padding-left: 5px;
  }
  
  .bound-control-cell {
    width: 95%;
    padding-left: 5px;
  }  
}

@media (min-width: 577px) {
  .bound-control-row {
    display: flex;
    flex-direction: row;
  }
  
  .bound-control-cell-label {
    width: 30%;
    justify-items: left;
    align-items: flex-start;
  }
  
  .bound-control-cell {
    width: 70%;
    align-items: flex-start;
    justify-items: left;
  }  
}

.mat-pagination {
  border-top: 1px solid lightgray;
}

.bound-control-cell {
  .MuiInputBase-inputMultiline {
    width: 68vw;
  }
}

.MuiDialogContent-root {
  .MuiInputBase-inputMultiline {
    width: 450px;
  } 
}

.dataentry-validation {
  padding: 15px 4%;

  .MuiInputBase-inputMultiline {
    width: 70vw;
  }
}
